import {
    Card,
    Box,
    CardContent,
    CardActionArea,
    Typography,
    Button,
  } from "@mui/material";
  const Task = ({ task }) => {

    // let bg="";
    // if(task.status==="pending")
    // {
    //   bg="#FFFFFF"
    // }else{
    //   bg="#00FF00"
    // }
    return (
      <Box sx={{ minWidth: 275 }} >
        <Card >
          <CardActionArea>
            <CardContent>
              <Typography gutterBottom variant="h5" component="div">
                {task.name}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Date: {task.date.split("T")[0]}
                
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Time: {task.time}
              </Typography>
              <Typography sx={{ p: 0 }} color="text.secondary">{task.type}</Typography>
            </CardContent>
          </CardActionArea>
        </Card>
      </Box>
    );
  };
  export default Task;